.section {
    display: flex;
    align-items: center;
    border-bottom-width: 2px;
    padding: 15px;
    min-width: 200px;
    cursor: pointer;
    color: grey;
    border-width: 0 !important;
}

.section > h4{
    color: grey;
}

.section > .MuiSvgIcon-root{
    color: grey;
}

.section--selected {
    background-color: whitesmoke;
    border-width: 3px !important;
}

.section:hover{
    background-color: whitesmoke;
    border-width: 3px !important;
}